

.container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    padding: 50px 20px;

    .gallery {
        padding: 100px 0;

        .sliderWrap {
            position: fixed;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            z-index: 1;
            
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
            height: 100%;

            background: none;
            
            .buttons {
                position: fixed;
                z-index: 4;

            .btnClose, .btnPrev, .btnNext {
                position: fixed;
                cursor: pointer;
                opacity: 0.6;
                color: #fff;
                
                width: 50px;
                height: 50px;
            }

            .btnNext:hover, .btnPrev:hover, .btnClose:hover {
                opacity: 1;
            }

            .btnClose {
                top: 40px;
                right: 40px;
            }

            .btnPrev {
                top: 50%;
                transform: translateY(-50%);
                left: 40px;
            }

            .btnNext {
                top: 50%;
                transform: translateY(-50%);
                right: 40px;
            }
            }

            .background {
                position: fixed;
                width: 100vw;
                height: 100vh;
                z-index: 2;
                background: rgba(0, 0, 0, 0.8);
                // background: none;

            }       

            .fullScreenImage {
                margin: 0 20px;
                display: flex;
                align-items: center;
                justify-content: center;
                z-index: 3;
                background: none;

                img {
                    max-width: 100%;
                    max-height: 100%;
                    pointer-events: none;
                    -webkit-user-select: none; /* Safari */
                    -ms-user-select: none; /* IE 10 and IE 11 */
                    user-select: none; /* Standard syntax */  
                }
            }
        }

        .galleryWrap {
            display: flex;
            flex-wrap: wrap;
            gap: 10px;
            align-items: center;
            justify-content: center;
            max-width: 620px;
            margin: 0 auto;

            .single {
                width: 100%;
                cursor: pointer;

                img {
                    max-width: 100%;
                    transition: 0.1s ease-in-out;
                }

                img:hover {
                    transform: scale(1.05);
                }
            }
        }

    }
}

@media screen and (min-device-width: 768px) {

.container {

    .gallery {

        .sliderWrap {


            .fullScreenImage {
                max-width: 500px;

                img {
                    
                }
            }
        }

        .galleryWrap {
            max-width: 1000px;

            .single {
                width: 200px;
            }

        }
    }
}
}